import React, { FC, useRef, useState, useEffect } from 'react';
import { useIntersectionObserver } from 'react-intersection-observer-hook';
import './styles.scss';

import 'swiper/css/pagination';
import 'swiper/css/navigation';
import 'swiper/css/free-mode';
import 'swiper/css/effect-coverflow';

import { Swiper, SwiperSlide, useSwiper } from 'swiper/react';
import { Navigation, EffectCoverflow, FreeMode } from 'swiper';
import { ITariff } from '../../types';
import { CarouselTariffItem } from './CarouselTariffItem';
import SvgArrowSlide from '../icons/ArrowSlide';

import { Link, useLocation } from 'react-router-dom';

interface IData {
  data: Array<ITariff>;
  item?: ITariff;
  onClick?: () => void;
}

const CarouselTariff: FC<IData> = ({ data, onClick }) => {
  const location = useLocation();
  const [_, setInit] = useState(false);

  const [ref, { entry }] = useIntersectionObserver();
  const isVisible = entry && entry.isIntersecting;

  const prevRef = useRef(null);
  const nextRef = useRef(null);

  const minWidth = window.matchMedia('(min-width: 576px)').matches;
  const swiperRef: any = useRef();
  const swiper = useSwiper();
  // State to force a rerender.
  const [, setSignal] = useState({});
  const forceRerender = () => setSignal({});

  useEffect(() => {
    if (swiper) swiper.on('slideChange', forceRerender);
    
  }, [isVisible]);

  
  const [, setActiveIndex] = useState(1);
  const onSlideChange = (index: number) =>
    data.length > 1 && setActiveIndex(index);

  return minWidth ? (
    <Swiper
      observer={true}
      observeParents={true}
      onBeforeInit={(swiper) => {
        swiperRef.current = swiper;
      }}
      onSlideChange={(swiper) => onSlideChange(swiper.activeIndex + 1)}
      freeMode={true}
      spaceBetween={40}
      loop={true}
      effect={'coverflow'}
      grabCursor={true}
      slidesPerView={'auto'}
      slidesOffsetAfter={2}
      coverflowEffect={{
        rotate: 2,
        stretch: 0,
        depth: 100,
        modifier: 2.5,
        slideShadows: true,
      }}
      navigation={{
        prevEl: prevRef.current,
        nextEl: nextRef.current,
      }}
      onInit={() => setInit(true)}
      modules={[EffectCoverflow, Navigation, FreeMode]}
      breakpoints={{
        320: {
          slidesPerView: 1,
          spaceBetween: 10,
        },
        640: {
          slidesPerView: 2,
          spaceBetween: 20,
        },
        992: {
          slidesPerView: 3,
        },
        1440: {
          slidesPerView: 3,
        },
      }}
      className='slider-tariff'
    >
      {data.length > 0
        ? data.map((item: ITariff, index) => (
            <SwiperSlide key={index} className='slide' data-id={item.id}>
              <CarouselTariffItem item={item}>
                <Link
                  onClick={onClick}
                  to={`/${item.id}`}
                  state={{ item: item, bgTariff: location }}
                  className='link-btn text-center'
                >
                  
                </Link>
              </CarouselTariffItem>
            </SwiperSlide>
          ))
        : null}

      <button
        className={`btn-prev swiper-button-prev`}
        ref={prevRef}
        aria-label=' '
      >
        <SvgArrowSlide />
      </button>
      <button
        className={`btn-next swiper-button-next`}
        ref={nextRef}
        aria-label=' '
      >
        <SvgArrowSlide />
      </button>
    </Swiper>
  ) : (
    <div className='products'>
      {data.length > 0
        ? data.slice(0, 4).map((item: ITariff, index) => (
            <div key={index} data-id={item.id} className='mb-4'>
              <CarouselTariffItem item={item}>
                <Link
                  onClick={onClick}
                  to={`/${item.id}`}
                  state={{ item: item, bgTariff: location }}
                  className='link-btn text-center'
                >
                  
                </Link>
              </CarouselTariffItem>
            </div>
          ))
        : null}
    </div>
  );
};

export { CarouselTariff };
