import React, { FC, useEffect, useState, useRef } from 'react';
import { useIntersectionObserver } from 'react-intersection-observer-hook';
import './styles.scss';
import { Swiper, SwiperSlide, useSwiper } from 'swiper/react';

import 'swiper/css/scrollbar';
import 'swiper/css/free-mode';
import 'swiper/css/effect-coverflow';
import SwiperCore, { Scrollbar, EffectCoverflow, FreeMode } from 'swiper';

interface ICarouselPictureItem {
  srcPic: string;
  altPic?: string;
  srcsetXL?: string;
  srcsetSM?: string;
  srcsetXS?: string;
}
interface ICarouselPicture {
  dataPic: Array<ICarouselPictureItem>;
  sliderId: string;
}

const CarouselPicture: FC<ICarouselPicture> = ({ dataPic, sliderId }) => {
  const [ref, { entry }] = useIntersectionObserver();
  const isVisible = entry && entry.isIntersecting;

  SwiperCore.use([Scrollbar, EffectCoverflow, FreeMode]);
  const swiper = useSwiper();
  const swiperRef: any = useRef();
  // State to force a rerender.
  const [, setSignal] = useState({});
  const forceRerender = () => setSignal({});

  useEffect(() => {
    if (swiper) swiper.on('slideChange', forceRerender);
  }, [isVisible]);

  return (
    <div ref={ref}>
      <Swiper
        observer={true}
        observeParents={true}
        id={sliderId}
        onBeforeInit={(swiper) => {
          swiperRef.current = swiper;
        }}
        // onInit={(swiper) => console.log('Swiper initialized!', swiper)}
        // onSlideChange={(swiper) => {
        //   console.log('Slide index changed to: ', swiper.activeIndex);
        // }}
        // onReachEnd={() => console.log('Swiper end reached')}
        freeMode={false}
        slidesPerView={3}
        spaceBetween={10}
        loop={true}
        scrollbar={{
          hide: true,
        }}
        coverflowEffect={{
          rotate: 20,
          stretch: 0,
          depth: 200,
          modifier: 5.5,
          slideShadows: true,
        }}
        modules={[Scrollbar, EffectCoverflow, FreeMode]}
        breakpoints={{
          320: {
            slidesPerView: 1.3,
            spaceBetween: 20,
          },
          640: {
            slidesPerView: 2,
          },
          1024: {
            slidesPerView: 3,
            freeMode: true,
          },
          1300: {
            slidesPerView: 3.3,
          },
        }}
        className='slider-gallery'
      >
        {dataPic.map((item, index) => (
          <SwiperSlide className='slide' key={index}>
            <picture className='img-fluid rounded'>
              <source
                type='image/webp'
                media='min-width: 1200px'
                srcSet={item.srcsetXL}
              />
              <source
                type='image/webp'
                media='min-width: 768px'
                srcSet={item.srcsetSM}
              />

              <img
                srcSet={item.srcsetXS}
                src={item.srcPic}
                alt={item.altPic}
                width='262'
                height='190'
                className='img-fluid rounded'
                loading='lazy'
              />
            </picture>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export { CarouselPicture };
