import React, { FC, useState, useEffect, useRef } from 'react';
import './styles.scss';
import Container from 'react-bootstrap/Container';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { LogoLink } from '../UI/logo-link/LogoLink';
import { Nav } from '../nav/Nav';
import SvgClose from '../icons/Close';
import SvgIconMenu from '../icons/Menu';

const PageHeader: FC = () => {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [sticky, setSticky] = useState<{ isSticky: boolean; offset: number }>({
    isSticky: false,
    offset: 0,
  });
  const headerRef = useRef<HTMLDivElement | null>(null);

  // handle scroll event
  const handleScroll = (elTopOffset: number, elHeight: number): void => {
    if (window.pageYOffset > elTopOffset + elHeight) {
      setSticky({ isSticky: true, offset: elHeight });
    } else {
      setSticky({ isSticky: false, offset: 0 });
    }
  };

  // add/remove scroll event listener
  useEffect(() => {
    var header = headerRef.current?.getBoundingClientRect();
    const handleScrollEvent = (): void => {
      handleScroll(header?.top || 0, header?.height || 0);
    };
    window.addEventListener('scroll', handleScrollEvent);
    return () => {
      window.removeEventListener('scroll', handleScrollEvent);
    };
  }, []);

  return (
    <>
      <header
        id='sticky-header'
        className={`page-header ${sticky.isSticky ? 'sticky' : ''}`}
        ref={headerRef}
      >
        <Container>
          <div className='page-header__label'>  </div>
          <LogoLink />
          <button
            type='button'
            className='page-header__btn-menu'
            aria-label=' '
            onClick={handleShow}
          >
            <SvgIconMenu />
          </button>
        </Container>
      </header>

      <Offcanvas
        show={show}
        onHide={handleClose}
        placement={'end'}
        restoreFocus={false}
      >
        <Offcanvas.Header>
          <Offcanvas.Title>
            <LogoLink />
            <small>  </small>
          </Offcanvas.Title>

          <button
            type='button'
            className='btn-close text-reset'
            aria-label='Close'
            onClick={handleClose}
          >
            <SvgClose />
          </button>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <Nav close={handleClose} />
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export { PageHeader };
