import React, { FC } from 'react';
import { useNavigate } from 'react-router-dom';

import { Promo } from '../../components/promo/Promo';
import { tariffMinPrice } from '../../utils/dataTariff';

import { useModal } from '../../hooks/useModal';
import Modal from '../../components/modal/Modal';
import ModalContent from '../../components/modal/ModalContent';
import { CustomForm } from '../../components/UI/custom-form/CustomForm';

const TariffPage: FC = () => {
  const navigate = useNavigate();

  const onCloseModal = () => {
    closeModal();
    navigate(-1);
  };
  const { openModal, closeModal, isModalOpen } = useModal();
  return (
    <>
      <main className='main'>
        <Promo onClick={openModal} minTariff={tariffMinPrice} />
      </main>
      <Modal onClose={onCloseModal} title=' ' classBodyScroll='modal-dialog-scrollable'>
        <ModalContent>
          {!isModalOpen ? <CustomForm onClose={onCloseModal} /> : null}
        </ModalContent>
      </Modal>
    </>
  );
};

export default TariffPage;
