import React, { FC, useMemo, useCallback } from 'react';
import { Form, Formik } from 'formik';
import { useParams } from 'react-router-dom';
import './styles.scss';

import { Button } from '../button/Button';
import { tariffData } from '../../../utils/dataTariff';
import { CustomInput } from '../custom-fields/CustomInput';
import { CustomSelect } from '../custom-fields/CustomSelect';
import { CustomInputPhone } from '../custom-fields/CustomInputPhone';
import { orderSchema } from '../../../schemas';

import { CustomInputCheckbox } from '../custom-fields/CustomInputCheckbox';
import { CustomInputTooltip } from '../custom-fields/CustomInputTooltip';
// import { generalRequest } from '../../../services/api';

// types
import { IFormValues, IProductValues } from '../../../types/index';
// import { BASE_URL } from '../../../utils/constants';

const CustomForm: FC<IFormValues> = () => {
  const { id } = useParams();
  const item = [...tariffData].find((item) => item.id === Number(id));

  const productValues: Array<IProductValues> = [];
  item &&
    [...item.products].map((el) => {
      return productValues.push({
        name: el.product_name,
        price: el.product_price,
        price_id: el.product_price_id,
      });
    });

  const phoneWithoutFormatting = (str: string) => {
    return str
      .replace(/[^0-9]/g, '')
      .slice(1)
      .trim();
  };

  const dataForm = useCallback(
    (values: any): any => {
      return {
        products: productValues,
        customer: {
          first_name: values.firstName,
          last_name: values.lastName,
          middle_name: values.middleName,
          birth_day: values.birthDay,
          phone: phoneWithoutFormatting(values.phone),
          email: values.email,
        },
        recurrentable: true,
      };
    },
    [productValues],
  );

  const optionsRequestData = useCallback(
    (values: any): any => {
      return {
        method: 'POST',
        mode: 'no-cors',
        headers: {
          'Content-Type': 'application/json;charset=utf-8',
        },
        body: JSON.stringify(dataForm(values), null, 2),
        redirect: 'follow',
      };
    },
    [dataForm],
  );

  const onSubmit = useCallback(
    async (values: any, actions: any): Promise<void> => {
      actions.setSubmitting(true);

      await fetch('https://echo.htmlacademy.ru/', optionsRequestData(values));

      // window.location.replace('https://echo.htmlacademy.ru/');
      setTimeout(() => {
        actions.resetForm();
      }, 300);
    },
    [optionsRequestData],
  );

  const formikProps = useMemo(
    () => ({
      initialValues: {
        firstName: '',
        lastName: '',
        middleName: '',
        clubs: '',
        phone: '',
        birthDay: '',
        email: '',
        isCheck: false,
      },
      onSubmit,
      validationSchema: orderSchema,
    }),
    [onSubmit],
  );

  return (
    <Formik {...formikProps}>
      {({ isSubmitting }) => (
        <Form {...onSubmit}>
          <div className='row'>
            <div className='col-md-6'>
              <CustomInput
                label=''
                name='firstName'
                type='text'
                placeholder='  '
              />
            </div>
            <div className='col-md-6'>
              <CustomInput
                label=''
                name='lastName'
                type='text'
                placeholder='  '
              />
            </div>
          </div>

          <CustomInput
            label='Email'
            name='email'
            type='email'
            placeholder='  email'
          />

          <CustomInputPhone
            label=''
            name='phone'
            type='tel'
            placeholder='  '
          />

          <CustomInputTooltip
            label=' '
            name='birthDay'
            type='date'
            id='customer_birth_day'
            textTooltip='       14 '
            placeholder=' '
            min='1900-01-01'
            max='2009-01-01'
          />

          {item && item.countClubs !== 4 && (
            <CustomSelect label='' name='clubs' placeholder=' '>
              <option disabled value=''>
                 
              </option>
              {item &&
                [...item.clubs].map((club) => (
                  <option value={club.id} key={club.address}>
                    {club.address}
                  </option>
                ))}
            </CustomSelect>
          )}

          <CustomInputCheckbox
            type='checkbox'
            name='isCheck'
            label='(-) (-) '
          />

          <Button btnType='submit' disabled={isSubmitting}>
            
          </Button>
        </Form>
      )}
    </Formik>
  );
};

export { CustomForm };
