import React, { FC } from 'react';
import './styles.scss';
import { IClub, ITariff } from '../../types';
import { formatPrice } from '../../utils/dataTariff';

interface IItem {
  item?: ITariff;
  children?: any;
}

const CarouselTariffItem: FC<IItem> = ({ item, children }) => {
  return !!item ? (
    <div className='product' data-price={item?.price}>
      <div className='product__wrapper'>
        <h3>{item?.title}</h3>
        <div className='product__price'>
          {formatPrice(item?.price)} <span> /</span>
        </div>
        <div className='product__text'>
          {item?.countClubs === 4 ? (
            <p>    4 </p>
          ) : (
            <p>    1  </p>
          )}

          <ul>
            {!!item.clubs &&
              item?.clubs.map((el: IClub, index) => (
                <li key={index}>{el.address}</li>
              ))}
          </ul>

          {!!item?.subscription ? (
            <div>*     5 000.</div>
          ) : null}
        </div>

        <ul className='product__list'>
          <li> </li>
          <li></li>
          <li> </li>
          <li>-</li>
          <li> </li>
          <li> </li>
        </ul>

        {children}
        {/* <Button btnType='button' onClick={onClickModal}></Button> */}
      </div>
    </div>
  ) : null;
};

export { CarouselTariffItem };
