import React, { FC } from 'react';
import './styles.scss';
import { Link } from 'react-router-dom';

const navData = [
  {
    path: '#profit',
    title: ' ',
  },
  {
    path: '#gallery',
    title: '',
  },
  {
    path: '#tariff',
    title: '',
  },
  {
    path: '#faq_view',
    title: '  ',
  },
];

interface INav {
  close: () => void;
}

const Nav: FC<INav> = ({ close }) => {
  const handelClick = (e: any) => {
    close();

    const nameId = e.target.getAttribute('href').slice(3);
    console.log(nameId);
    document.querySelector('[id="' + nameId + '"]')?.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
      inline: 'nearest',
    });
  };
  return (
    <nav className='navigation'>
      <ul className='navigation__list'>
        {navData.map((item) => (
          <li key={item.path} className='navigation__item'>
            <Link
              className='navigation__link'
              onClick={handelClick}
              to={item.path}
            >
              {item.title}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export { Nav };
