/* eslint-disable */
import { object, string, date, bool } from 'yup';
import { clubsAddress } from '../utils/dataTariff';
import 'yup-phone-lite';

const phoneRegex =
  /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,3})[ \\-]*)*?[0-9]{2,4}?[ \\-]*[0-9]{2,4}?$/;

const emailRegex =
  /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export const orderSchema = object().shape({
  firstName: string()
    .trim()
    .min(2, '  ')
    .required(' '),
  lastName: string()
    .trim()
    .min(2, '  ')
    .required(' '),
  email: string()
    .matches(emailRegex, '  , : test@test.ru')
    .email()
    .required(' '),
  birthDay: date()
    .nullable()
    .max(
      new Date(Date.now() - 441504000000),
      '       14 ',
    )
    .required(' '),
  isCheck: bool()
    .oneOf([true], '   ')
    .required(' '),
  clubs: string()
    .oneOf(
      [
        clubsAddress[0].id,
        clubsAddress[1].id,
        clubsAddress[2].id,
        clubsAddress[3].id,
      ],
      '  ',
    )
    .required('  '),
  phone: string()
    .min(11)
    .phone('RU', '    ')
    .required(' '),
});
